body {
  font-family: 'Axiforma', sans-serif;
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

source {
  width: 0;
  height: 0;
}

.container {
  width: 100%;
  margin: 0 auto;
}

@media screen and (min-width: 1576px) {
  :root {
    font-size: 18px;
    transition: font-size .3s ease;
  }
}

@media screen and (min-width: 1735px) {
  :root {
    font-size: 19.3px;
    transition: font-size .3s ease;
  }
}

@media screen and (min-width: 1921px) {
  :root {
    font-size: 22px;
    transition: font-size .3s ease;
  }
}

@media screen and (min-width: 2140px) {
  :root {
    font-size: 24px;
    transition: font-size .3s ease;
  }
}

@media screen and (max-width: 1575px) {
  :root {
    font-size: 16.4px;
    transition: font-size .3s ease;
  }
}

@media screen and (max-width: 1440px) {
  :root {
    font-size: 16px;
    transition: font-size .3s ease;
  }
}

@media screen and (max-width: 768px) {
  :root {
    font-size: 14px;
    transition: font-size .3s ease;
  }
}

@media screen and (max-width: 425px) {
  :root {
    font-size: 12px;
    transition: font-size .3s ease;
  }
}

@media screen and (max-width: 375px) {
  :root {
    font-size: 12px;
    transition: font-size .3s ease;
  }
}

@font-face {
  font-family: 'Axiforma';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Axiforma-Regular.woff2") format("woff2"), url("../fonts/Axiforma-Regular.woff") format("woff");
}

@font-face {
  font-family: 'Axiforma';
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/Axiforma-Bold.woff2") format("woff2"), url("../fonts/Axiforma-Bold.woff") format("woff");
}

@font-face {
  font-family: 'Axiforma';
  font-style: normal;
  font-weight: 900;
  font-display: swap;
  src: url("../fonts/Axiforma-Black.woff2") format("woff2"), url("../fonts/Axiforma-Black.woff") format("woff");
}

body {
  font-family: 'Axiforma', sans-serif;
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

source {
  width: 0;
  height: 0;
}

.container {
  width: 100%;
  margin: 0 auto;
}

.header {
  height: 3.125rem;
  margin: 0 8.563rem 0 1.25rem;
  padding-top: 2rem;
  padding-bottom: 0.625rem;
  position: relative;
  z-index: 50;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  max-width: 90rem;
}

.header svg {
  height: 100%;
}

.header a {
  font-size: 1.125rem;
  font-weight: 700;
  color: #d40f14;
  height: inherit;
  display: flex;
  align-items: center;
  text-decoration: none;
}

.header__logo {
  height: inherit;
  display: flex;
  z-index: 10;
}

.header__logo svg {
  width: 11.5rem;
}

.header__navigation {
  width: 100%;
  margin: 0 2.375rem 0 3.313rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: inherit;
}

.header__contacts, .header__for {
  width: 50%;
  height: inherit;
  display: flex;
}

.header__contacts a:not(:first-child), .header__for a:not(:first-child) {
  margin-left: 2.188rem;
}

.header-contacts {
  justify-content: flex-end;
}

.header-contacts svg {
  width: 1.125rem;
  margin-right: 0.625rem;
}

@media screen and (max-width: 768px) {
  .header {
    width: calc(100% - 6.5rem);
    margin-right: 0;
  }
  .header__navigation {
    margin: 0 3rem 0 2rem;
  }
  .header__for {
    flex-direction: column;
    justify-content: space-between;
  }
  .header__for a:last-child {
    margin: 0;
  }
  .header__contacts {
    flex-direction: column;
  }
  .header__contacts a {
    height: 50%;
  }
  .header__contacts a:last-child {
    margin: 0;
  }
  .header__for a,
  .header__contacts a {
    font-size: 1.2rem;
  }
}

@media screen and (max-width: 720px) {
  .header__navigation {
    margin: 0 1rem 0 1rem;
  }
  .header__for {
    width: 45%;
  }
  .header__contacts {
    width: 55%;
  }
  .header__for a,
  .header__contacts a {
    font-size: 1rem;
    width: 100%;
  }
}

@media screen and (max-width: 600px) {
  .header__for {
    display: none;
  }
  .header__contacts {
    width: 100%;
    margin-left: 2rem;
  }
}

@media screen and (max-width: 480px) {
  .header__navigation {
    margin: 0 0 0 .5rem;
  }
  .header__contacts {
    margin-left: .5rem;
  }
}

@media screen and (max-width: 440px) {
  .header__navigation {
    display: none;
  }
}

@media screen and (max-width: 425px) {
  .header {
    width: calc(100% - 5.5rem);
    margin-right: 0;
  }
  .header__contacts {
    display: none;
  }
}

.sidebar {
  position: fixed;
  width: 7.375rem;
  height: 100vh;
  border-left: 0.063rem solid #d40f14;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  top: 0;
  right: 0;
  z-index: 5;
}

.sidebar__blank {
  width: 7.375rem;
  height: 6.5rem;
}

.sidebar__request {
  width: 5.438rem;
  height: 5.438rem;
  background-color: #d40f14;
  border-radius: 50%;
  font-size: 0.75rem;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  box-shadow: 0 0 0 0 black;
  transform: scale(1);
  animation-play-state: paused;
}

.sidebar__request:hover {
  cursor: pointer;
  animation: pulse 2s infinite;
}

.sidebar__request p {
  margin: 0;
  height: 2.125rem;
}

.sidebar__arrow-top {
  margin-bottom: 6.875rem;
}

.sidebar__arrow-top svg {
  height: 2.75rem;
  width: 2.75rem;
}

.sidebar__arrow-top .arrow-use path {
  animation: arrow-top 2s linear infinite;
}

.sidebar__arrow-top:hover {
  cursor: pointer;
}

.burger-wrapper {
  width: 7.375rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 52;
}

.burger-wrapper .burger {
  position: fixed;
  z-index: 10;
  top: 2rem;
  width: 2.75rem;
  height: 2.75rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 0.125rem solid #d40f14;
  border-radius: 50%;
  transition: border-color 0.4s ease;
}

.burger-wrapper .burger:hover {
  cursor: pointer;
}

.burger-wrapper .burger span {
  position: absolute;
  max-width: 1.688rem;
  width: 100%;
  height: 0.125rem;
  display: block;
  background-color: #d40f14;
  transition: transform 0.4s ease, background-color 0.4s ease, opacity 0.2s ease, margin 0.5s ease;
}

.burger-wrapper .burger span:first-child {
  bottom: 0.875rem;
}

.burger-wrapper .burger span:last-child {
  top: 0.865rem;
}

.dropdown {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: #d40f14;
  top: 0;
  left: 0;
  transform: translateY(-250%);
  transition: transform 0.4s ease-in-out;
  z-index: 51;
}

.dropdown.down {
  transform: translateY(0);
  transition: transform 0.4 ease-in-out;
}

.dropdown__header {
  display: flex;
  align-items: center;
  height: 3.125rem;
  margin: 2rem 8.563rem 0 1.25rem;
}

.dropdown__header svg {
  width: 11.5rem;
  height: inherit;
}

.dropdown__menu {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 80%;
  margin-top: 2.875rem;
}

.dropdown__item {
  font-size: 3rem;
  font-weight: 700;
  line-height: 3.5rem;
  text-transform: uppercase;
  color: transparent;
  -webkit-text-stroke: 2px #fff;
  transition: color 0.4s ease;
}

.dropdown__item:not(:first-child) {
  margin-top: 2rem;
}

.dropdown__item:hover {
  cursor: pointer;
  transition: color 0.4s ease;
  color: #fff;
}

.dropdown__right-side {
  position: absolute;
  right: 0;
  top: 0;
  width: 7.375rem;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
}

.dropdown__social {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 17.875rem;
}

.dropdown__social :not(:first-child) {
  margin-top: 0.938rem;
}

.dropdown__social a {
  width: 2.5rem;
  height: 2.5rem;
}

.dropdown__social svg {
  width: inherit;
  height: inherit;
  border-radius: 1.25rem;
  box-shadow: 0.25rem 0.25rem 0.938rem rgba(0, 0, 0, 0.2);
}

.dropdown__contacts {
  width: 13.375rem;
  display: flex;
  flex-direction: column;
  position: absolute;
  margin-bottom: 5.7rem;
  margin-right: 25.75rem;
}

.dropdown__contacts a {
  display: flex;
  align-items: center;
  font-size: 1.125rem;
  font-weight: 700;
  line-height: 1.875rem;
  text-decoration: none;
  color: #fff;
}

.dropdown__contacts svg {
  width: 1.125rem;
  height: 1.125rem;
  margin-right: 0.625rem;
}

.center-line {
  position: fixed;
  z-index: -4;
  height: 100vh;
  top: 0;
  left: 50%;
  border-right: 0.063rem solid #d40f14;
}

@keyframes arrow-top {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(20px);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes pulse {
  0% {
    transform: scale(0.95);
    box-shadow: 0 0 0 0 rgba(215, 15, 22, 0.7);
  }
  70% {
    transform: scale(1);
    box-shadow: 0 0 0 10px rgba(215, 15, 22, 0);
  }
  100% {
    transform: scale(0.95);
    box-shadow: 0 0 0 0 rgba(215, 15, 22, 0);
  }
}

@media screen and (max-width: 768px) {
  .sidebar {
    height: 100%;
    width: 5rem;
  }
  .sidebar__blank {
    width: 5rem;
  }
  .sidebar__request {
    width: 4rem;
    height: 4rem;
  }
  .sidebar__request p {
    font-size: .5rem;
    height: 1.5rem;
  }
  .center-line {
    height: 100%;
  }
  .burger-wrapper {
    width: 5rem;
  }
  .burger-wrapper .burger span {
    height: 2px;
  }
}

@media screen and (max-width: 425px) {
  .sidebar {
    width: 4.2rem;
  }
  .sidebar__blank {
    width: 4.2rem;
  }
  .sidebar__request {
    width: 3.15rem;
    height: 3.15rem;
  }
  .sidebar__request p {
    font-size: .5rem;
    height: 2rem;
  }
  .burger-wrapper {
    width: 4.2rem;
  }
  .burger-wrapper .burger span {
    height: 2px;
  }
}

@media screen and (max-width: 375px) {
  .sidebar {
    width: 4.133rem;
  }
  .sidebar__blank {
    width: 4.133rem;
  }
  .sidebar__request {
    width: 3.138rem;
    height: 3.138rem;
  }
  .sidebar__request p {
    font-size: .45rem;
    height: 1.5rem;
  }
  .burger-wrapper {
    width: 4.133rem;
  }
  .dropdown__item {
    font-size: 2.5rem;
    -webkit-text-stroke-width: 1px;
  }
  .dropdown__menu {
    height: 60%;
  }
}

.left-side-bar {
  position: fixed;
  width: 7.375rem;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 0;
  background-color: #fff;
  border-right: 0.063rem solid #d40f14;
}

@media screen and (max-width: 768px) {
  .left-side-bar {
    width: 5rem;
  }
}

@media screen and (max-width: 480px) {
  .left-side-bar {
    display: none;
  }
}
